// src/components/ui/chart.jsx
"use client"

import * as React from "react"
import { Legend, Tooltip } from "recharts"

import { cn } from "@/lib/utils"

const ChartConfigContext = React.createContext(null)

export function useChartConfig() {
  const context = React.useContext(ChartConfigContext)
  if (!context) {
    throw new Error("Chart components must be used within a <ChartContainer />")
  }
  return context
}

export function ChartContainer({ config, className, children, ...props }) {
  return (
    <ChartConfigContext.Provider value={config}>
      <div className={cn("flex h-full w-full flex-1", className)} {...props}>
        {children}
      </div>
    </ChartConfigContext.Provider>
  )
}

export function ChartTooltip({ content, ...props }) {
  const config = useChartConfig()

  return (
    <Tooltip
      cursor={{ stroke: "hsl(var(--muted))", strokeWidth: 1, strokeDasharray: "3 3" }}
      content={(tooltipProps) => {
        if (!content) {
          return <ChartTooltipContent {...tooltipProps} config={config} />
        }
        return React.cloneElement(content, { ...tooltipProps, config })
      }}
      {...props}
    />
  )
}

export function ChartTooltipContent({ active, payload, label, config = {} }) {
  const mergedConfig = config ?? {}
  if (!active || !payload || payload.length === 0) return null

  return (
    <div className="rounded-lg border bg-background/95 px-3 py-2 text-sm shadow-sm">
      {label ? <div className="mb-1 font-medium">{label}</div> : null}
      <div className="grid gap-1">
        {payload.map((item) => {
          const itemConfig = mergedConfig?.[item.dataKey] ?? {}
          const color = item.color ?? itemConfig.color ?? "hsl(var(--foreground))"
          const name = itemConfig.label ?? item.name ?? item.dataKey
          const sendKey = `${item.dataKey}__send`
          const rawSend = item.payload ? item.payload[sendKey] : undefined
          const sendJiraLabel = typeof rawSend === "number" ? ` Â· JIRA ${numberFormatter.format(rawSend)}` : ""

          return (
            <div key={item.dataKey} className="flex items-center justify-between gap-4">
              <div className="flex items-center gap-2">
                <span className="h-2 w-2 rounded-full" style={{ backgroundColor: color }} />
                <span className="text-muted-foreground">{name}</span>
              </div>
              <span className="font-semibold text-foreground">
                {numberFormatter.format(item.value)}
                {sendJiraLabel}
              </span>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export function ChartLegend({ content, ...props }) {
  const config = useChartConfig()

  return (
    <Legend
      verticalAlign="top"
      align="left"
      content={(legendProps) => {
        if (!content) {
          return <ChartLegendContent {...legendProps} config={config} />
        }
        return React.cloneElement(content, { ...legendProps, config })
      }}
      {...props}
    />
  )
}

export function ChartLegendContent({ payload, config = {} }) {
  const mergedConfig = config ?? {}
  if (!payload || payload.length === 0) return null

  return (
    <div className="flex flex-wrap items-center gap-x-4 gap-y-2 text-xs">
      {payload.map((entry) => {
        const itemConfig = mergedConfig?.[entry.dataKey] ?? {}
        const color = entry.color ?? itemConfig.color ?? "hsl(var(--foreground))"
        const label = itemConfig.label ?? entry.value ?? entry.dataKey
        return (
          <div key={entry.dataKey} className="flex items-center gap-2 text-muted-foreground">
            <span className="h-2 w-2 rounded-full" style={{ backgroundColor: color }} />
            <span>{label}</span>
          </div>
        )
      })}
    </div>
  )
}

const numberFormatter = new Intl.NumberFormat("ko-KR")
