/* ============================================================================
 * NeedToSendCell
 * - needtosend(0/1) ê°’ì„ í† ê¸€í•˜ëŠ” ì›í˜• ë²„íŠ¼ ì…€
 * - ì €ì¥ ì„±ê³µ/ì·¨ì†Œ/ì‹¤íŒ¨ì— ë”°ë¼ í† ìŠ¤íŠ¸ ë©”ì‹œì§€ í‘œì‹œ
 * - ë¹„í™œì„±(disabled)ì´ë©´ í´ë¦­/í‚¤ë³´ë“œ í† ê¸€ ì°¨ë‹¨
 * - ì ‘ê·¼ì„±(a11y): role="switch", aria-checked, í‚¤ë³´ë“œ(Enter/Space) ì§€ì›
 * ========================================================================== */

/* =========================
 * 1) ê³µí†µ ìƒìˆ˜/ìœ í‹¸
 * ======================= */

/** ì •ìˆ˜ 0/1ë¡œ ì•ˆì „ ë³€í™˜ (ê·¸ ì™¸ ê°’ì€ 0ìœ¼ë¡œ ì·¨ê¸‰) */
function to01(v) {
  const n = Number(v)
  return Number.isFinite(n) && n === 1 ? 1 : 0
}

/** í† ìŠ¤íŠ¸ ë„ìš°ë¯¸: ì„±ê³µ/ì •ë³´/ì‹¤íŒ¨ ê°ê° ê°„ë‹¨í•œ í—¬í¼ë¡œ ë˜í•‘ */
function showReserveToast() {
  toast.success("ì˜ˆì•½ ì„±ê³µ", {
    description: "E-SOP Inform ì˜ˆì•½ ë˜ì—ˆìŠµë‹ˆë‹¤.",
    icon: <CalendarCheck2 className="h-5 w-5 text-blue-500" />,
    ...buildToastOptions({ color: "#065f46", duration: 1800 }),
  })
}

function showCancelToast() {
  toast("ì˜ˆì•½ ì·¨ì†Œ", {
    description: "E-SOP Inform ì˜ˆì•½ ì·¨ì†Œ ë˜ì—ˆìŠµë‹ˆë‹¤.",
    icon: <CalendarX2 className="h-5 w-5 text-sky-600" />,
    ...buildToastOptions({ color: "#1e40af", duration: 1800 }),
  })
}

function showErrorToast(msg) {
  toast.error("ì €ì¥ ì‹¤íŒ¨", {
    description: msg || "ì €ì¥ ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.",
    icon: <XCircle className="h-5 w-5 text-red-500" />,
    ...buildToastOptions({ color: "#991b1b", duration: 3000 }),
  })
}

/* =========================
 * 2) ì»´í¬ë„ŒíŠ¸
 * ======================= */
export function NeedToSendCell({
  meta,
  recordId,
  baseValue,
  disabled = false,
  disabledReason = "ì´ë¯¸ JIRA ì „ì†¡ë¨ (needtosend ìˆ˜ì • ë¶ˆê°€)",
}) {
  // ë©”íƒ€ì—ì„œ ì„ì‹œ ë“œë˜í”„íŠ¸ ê°’(ì‚¬ìš©ìê°€ í† ê¸€í–ˆìœ¼ë‚˜ ì„œë²„ ì €ì¥ ì „) ìš°ì„  ì‚¬ìš©
  const draftValue = meta?.needToSendDrafts?.[recordId]
  const nextValue = draftValue ?? baseValue
  const isChecked = to01(nextValue) === 1

  // ì €ì¥ ì¤‘ ìƒíƒœ: ê°™ì€ ì…€ì— ëŒ€í•œ ë™ì‹œ ìš”ì²­ ë°©ì§€
  const savingKey = makeCellKey(recordId, "needtosend")
  const isSaving = Boolean(meta?.updatingCells?.[savingKey])

  // â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
  // í† ê¸€ ë¡œì§ (í´ë¦­/í‚¤ë³´ë“œ ëª¨ë‘ ì´ ë¡œì§ í˜¸ì¶œ)
  // â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
  const toggle = async () => {
    // â›” ë¹„í™œì„± ë˜ëŠ” ì €ì¥ ì¤‘ì´ë©´ ì¦‰ì‹œ ì¤‘ë‹¨
    if (disabled) {
      toast.info(disabledReason)
      return
    }
    if (isSaving) return

    const targetValue = isChecked ? 0 : 1

    // ë“œë˜í”„íŠ¸/ì—ëŸ¬ ì´ˆê¸°í™”
    meta?.setNeedToSendDraftValue?.(recordId, targetValue)
    meta?.clearUpdateError?.(savingKey)

    try {
      // ì„œë²„ì— ì‹¤ì œ ì—…ë°ì´íŠ¸ ìš”ì²­ (ì„±ê³µ ì‹œ true ê°€ì •)
      const ok = await meta?.handleUpdate?.(recordId, { needtosend: targetValue })

      // ì„±ê³µ
      if (ok) {
        meta?.removeNeedToSendDraftValue?.(recordId)
        targetValue === 1 ? showReserveToast() : showCancelToast()
        return
      }

      // ì‹¤íŒ¨(ëª…ì‹œì  false)
      const msg = meta?.updateErrors?.[savingKey]
      showErrorToast(msg)
    } catch (err) {
      // ì˜ˆì™¸ ë°œìƒ ì‹œì—ë„ ë™ì¼í•˜ê²Œ ì‹¤íŒ¨ ì²˜ë¦¬
      showErrorToast(err?.message)
    } finally {
      // ì‹¤íŒ¨í–ˆë“  ì„±ê³µí–ˆë“  ë“œë˜í”„íŠ¸ëŠ” ì •ë¦¬(ì„±ê³µ ì‹œ ìœ„ì—ì„œ ì´ë¯¸ ì œê±°í–ˆì§€ë§Œ ì¤‘ë³µ ì œê±° OK)
      meta?.removeNeedToSendDraftValue?.(recordId)
    }
  }

  // â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
  // í‚¤ë³´ë“œ ì ‘ê·¼ì„±: Space/Enter ë¡œ í† ê¸€
  // â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
  const onKeyDown = (e) => {
    if (e.key === "Enter" || e.key === " ") {
      e.preventDefault()
      toggle()
    }
  }

  const titleText = disabled ? disabledReason : isChecked ? "Need to send" : "Not selected"

  return (
    <div className="inline-flex justify-center">
      {/* âœ… ì›í˜• í† ê¸€ ë²„íŠ¼ (role='switch' + aria-checkedë¡œ ìŠ¤í¬ë¦°ë¦¬ë” ì¹œí™”ì ) */}
      <button
        type="button"
        onClick={toggle}
        onKeyDown={onKeyDown}
        disabled={disabled || isSaving}
        role="switch"
        aria-checked={isChecked}
        aria-disabled={disabled || isSaving}
        aria-label={titleText}
        title={titleText}
        className={cn(
          "inline-flex h-5 w-5 items-center justify-center rounded-full border transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2",
          isChecked
            ? "bg-blue-500 border-blue-500"
            : "border-muted-foreground/30 hover:border-blue-300",
          (disabled || isSaving) && "bg-gray-400 border-gray-400 cursor-not-allowed"
        )}
      >
        {isChecked && <Check className="h-3 w-3 text-white" strokeWidth={3} />}
      </button>
    </div>
  )
}

export default NeedToSendCell
