// src/features/line-dashboard/hooks/useLineHistoryData.js
"use client"

import * as React from "react"

const DEFAULT_DAYS = 30

const createIdleState = () => ({ isLoading: false, error: null })

export function useLineHistoryData(initialLineId = "", initialDays = DEFAULT_DAYS) {
  const [lineId, setLineId] = React.useState(initialLineId)
  const [days, setDays] = React.useState(initialDays)
  const [data, setData] = React.useState(null)
  const [status, setStatus] = React.useState(createIdleState)
  const requestRef = React.useRef(0)

  const refresh = React.useCallback(
    async (overrides = {}) => {
      const targetLineId = overrides.lineId ?? lineId
      const targetDays = overrides.days ?? days
      if (!targetLineId) {
        setData(null)
        setStatus(createIdleState())
        return
      }

      const requestId = ++requestRef.current
      setStatus({ isLoading: true, error: null })

      try {
        const search = new URLSearchParams({ lineId: targetLineId })
        if (Number.isFinite(targetDays) && targetDays > 0) {
          search.set("days", String(targetDays))
        }
        const response = await fetch(`/api/line-dashboard/history?${search.toString()}`, { cache: "no-store" })
        if (!response.ok) {
          throw new Error(`Failed to load history (${response.status})`)
        }
        const payload = await response.json()
        if (requestRef.current !== requestId) return
        setData(payload)
        setStatus(createIdleState())
      } catch (error) {
        if (requestRef.current !== requestId) return
        const message = error instanceof Error ? error.message : "Unknown error"
        setStatus({ isLoading: false, error: message })
      }
    },
    [lineId, days]
  )

  React.useEffect(() => {
    setLineId(initialLineId)
  }, [initialLineId])

  React.useEffect(() => {
    setDays(initialDays)
  }, [initialDays])

  React.useEffect(() => {
    refresh()
  }, [refresh])

  return {
    lineId,
    setLineId,
    days,
    setDays,
    data,
    refresh,
    status,
  }
}
