  sdwt: "SDWT ê·¸ë£¹ ê¸°ì¤€ìœ¼ë¡œ ì¼ì¼ ì²˜ë¦¬ëŸ‰ì„ ë¹„êµí•©ë‹ˆë‹¤.",
  user_sdwt: "User SDWT ê¸°ì¤€ìœ¼ë¡œ ì‘ì—…ìë³„ í¸ì°¨ë¥¼ í™•ì¸í•©ë‹ˆë‹¤.",
  eqp_id: "ì„¤ë¹„(EQP) ë‹¨ìœ„ì˜ ì§„í–‰ ê±´ìˆ˜ë¥¼ ì¶”ì í•©ë‹ˆë‹¤.",
  main_step: "ë©”ì¸ ê³µì • ë‹¨ê³„ë³„ ì§„í–‰ ì¶”ì´ë¥¼ í™•ì¸í•©ë‹ˆë‹¤.",
  sample_type: "ìƒ˜í”Œ íƒ€ì…ë³„ ì‘ì—… íŒ¨í„´ì„ ì‚´í´ë´…ë‹ˆë‹¤.",
