export function LineHistoryDashboard({ lineId }) {
  const { data, days, setDays, refresh, status } = useLineHistoryData(lineId)
  const isLoading = status.isLoading
  const error = status.error

  const timeline = data?.dates ?? []
  const totalsSummary = data?.totals?.summary ?? { rows: 0, sendJira: 0 }

  const totalsConfig = React.useMemo(
    () => ({
      rows: { label: "ì´ ì§„í–‰ ê±´ìˆ˜", color: "hsl(var(--chart-1))" },
      sendJira: { label: "Send JIRA ê±´ìˆ˜", color: "hsl(var(--chart-2))" },
    }),
    []
  )

  const totalsChartData = React.useMemo(() => {
    const series = data?.totals?.series ?? []
    return series.map((item) => ({
      date: item.date,
      rows: item.rows,
      sendJira: item.sendJira,
    }))
  }, [data])

  const dimensionEntries = React.useMemo(() => {
    const dimensionMap = data?.dimensions
    if (!dimensionMap) return []
    return Object.entries(dimensionMap)
      .map(([key, value]) => ({ key, value }))
      .filter((entry) => entry.value?.series?.length > 0)
      .sort((a, b) => {
        const aTotal = a.value.series.reduce((acc, series) => acc + (series.totalRows ?? 0), 0)
        const bTotal = b.value.series.reduce((acc, series) => acc + (series.totalRows ?? 0), 0)
        return bTotal - aTotal
      })
  }, [data])

  const handlePresetChange = React.useCallback(
    (preset) => {
      if (preset === days) return
      setDays(preset)
    },
    [days, setDays]
  )

  return (
    <div className="flex h-full flex-col gap-4">
      <header className="flex flex-col gap-3 rounded-xl border bg-card p-4 shadow-sm lg:flex-row lg:items-center lg:justify-between">
        <div>
          <h1 className="text-lg font-semibold">E-SOP ì§„í–‰ íˆìŠ¤í† ë¦¬</h1>
          <p className="text-sm text-muted-foreground">
            {timeline.length > 0
              ? `${timeline[0]} ~ ${timeline[timeline.length - 1]} Â· ì´ ${NUMBER_FORMATTER.format(
                  totalsSummary.rows
                )}ê±´`
              : "ì„ íƒëœ ê¸°ê°„ ë™ì•ˆì˜ íˆìŠ¤í† ë¦¬ë¥¼ ë¶ˆëŸ¬ì˜µë‹ˆë‹¤."}
          </p>
        </div>
        <div className="flex flex-wrap items-center gap-2">
          <div className="flex items-center gap-1 rounded-full border bg-background p-1 text-xs">
            {RANGE_PRESETS.map((preset) => (
              <button
                key={preset}
                type="button"
                onClick={() => handlePresetChange(preset)}
                className={cn(
                  "rounded-full px-3 py-1 font-medium transition-colors",
                  preset === days
                    ? "bg-primary text-primary-foreground shadow"
                    : "text-muted-foreground hover:bg-muted"
                )}
              >
                {preset}ì¼
              </button>
            ))}
          </div>
          <Button size="sm" variant="outline" onClick={() => refresh()} disabled={isLoading}>
            <IconRefresh className={cn("mr-2 h-4 w-4", isLoading && "animate-spin")} /> ìƒˆë¡œê³ ì¹¨
          </Button>
        </div>
      </header>

      {error ? (
        <div className="rounded-xl border border-destructive/50 bg-destructive/5 p-4 text-sm text-destructive">
          {error}
        </div>
      ) : null}

      <section className="grid gap-4 lg:grid-cols-3">
        <div className="rounded-xl border bg-card p-4 shadow-sm lg:col-span-2">
          <div className="mb-3 flex items-center justify-between">
            <div>
              <h2 className="text-sm font-semibold">ì¼ë³„ ì§„í–‰ í˜„í™©</h2>
              <p className="text-xs text-muted-foreground">ì´ ì§„í–‰ ê±´ìˆ˜ì™€ Send JIRA ê±´ìˆ˜ë¥¼ í•¨ê»˜ ë¹„êµí•©ë‹ˆë‹¤.</p>
            </div>
            <span className="text-xs text-muted-foreground">
              ì´ {NUMBER_FORMATTER.format(totalsSummary.rows)}ê±´ Â· JIRA {NUMBER_FORMATTER.format(totalsSummary.sendJira)}ê±´
            </span>
          </div>
          <div className="h-[320px]">
            {isLoading && totalsChartData.length === 0 ? (
              <Skeleton className="h-full w-full" />
            ) : (
              <ChartContainer config={totalsConfig}>
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={totalsChartData} margin={{ top: 20, right: 16, left: 8, bottom: 8 }}>
                    <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                    <XAxis
                      dataKey="date"
                      tickFormatter={(value) => formatDateTick(value)}
                      className="text-xs text-muted-foreground"
                    />
                    <YAxis
                      width={60}
                      tickFormatter={(value) => NUMBER_FORMATTER.format(value)}
                      className="text-xs text-muted-foreground"
                    />
                    <ChartTooltip />
                    <ChartLegend />
                    <Line
                      type="monotone"
                      dataKey="rows"
                      stroke={totalsConfig.rows.color}
                      strokeWidth={2}
                      dot={false}
                      name="rows"
                    />
                    <Line
                      type="monotone"
                      dataKey="sendJira"
                      stroke={totalsConfig.sendJira.color}
                      strokeWidth={2}
                      dot={false}
                      name="sendJira"
                    />
                  </LineChart>
                </ResponsiveContainer>
              </ChartContainer>
            )}
          </div>
        </div>
        <div className="rounded-xl border bg-card p-4 shadow-sm">
          <h2 className="text-sm font-semibold">ìš”ì•½</h2>
          <div className="mt-3 grid gap-2 text-sm">
            <SummaryRow label="ì´ ì§„í–‰" value={totalsSummary.rows} />
            <SummaryRow label="Send JIRA" value={totalsSummary.sendJira} />
            <SummaryRow
              label="JIRA ë¹„ìœ¨"
              value={
                totalsSummary.rows > 0
                  ? `${((totalsSummary.sendJira / totalsSummary.rows) * 100).toFixed(1)}%`
                  : "-"
              }
            />
            <div className="mt-2 flex items-center gap-2 rounded-lg bg-muted/60 px-3 py-2 text-xs text-muted-foreground">
              <IconChevronRight className="h-4 w-4" /> ì•„ë˜ ì°¨íŠ¸ì—ì„œ ì„¸ë¶€ ê·¸ë£¹ë³„ ì¶”ì´ë¥¼ í™•ì¸í•´ ë³´ì„¸ìš”.
            </div>
          </div>
        </div>
      </section>

      <section className="grid gap-4 xl:grid-cols-2">
        {dimensionEntries.map(({ key, value }) => (
          <DimensionChartCard
            key={key}
            dimensionKey={key}
            dimension={value}
            timeline={timeline}
            isLoading={isLoading && value.series.length === 0}
          />
        ))}
        {dimensionEntries.length === 0 && !isLoading ? (
          <div className="rounded-xl border bg-card p-8 text-center text-sm text-muted-foreground">
            í‘œì‹œí•  íˆìŠ¤í† ë¦¬ ë°ì´í„°ê°€ ì—†ìŠµë‹ˆë‹¤.
          </div>
        ) : null}
      </section>
    </div>
  )
}

function DimensionChartCard({ dimensionKey, dimension, timeline, isLoading }) {
  const chartConfig = React.useMemo(() => {
    const entries = {}
    dimension.series.forEach((series, index) => {
      const color = CATEGORY_COLORS[index % CATEGORY_COLORS.length]
      entries[series.id] = { label: series.key, color }
    })
    return entries
  }, [dimension.series])

  const chartData = React.useMemo(() => {
    if (timeline.length === 0) return []
    return timeline.map((date, dateIndex) => {
      const point = { date }
      dimension.series.forEach((series) => {
        const entry = series.data?.[dateIndex] ?? { rows: 0, sendJira: 0 }
        point[series.id] = entry.rows
        point[`${series.id}__send`] = entry.sendJira
      })
      return point
    })
  }, [dimension.series, timeline])

  const totalRows = React.useMemo(
    () => dimension.series.reduce((acc, series) => acc + (series.totalRows ?? 0), 0),
    [dimension.series]
  )

  return (
    <div className="flex flex-col gap-3 rounded-xl border bg-card p-4 shadow-sm">
      <div className="flex items-start justify-between gap-2">
        <div>
          <h3 className="text-sm font-semibold">{DIMENSION_TITLES[dimensionKey] ?? dimension.label}</h3>
          <p className="text-xs text-muted-foreground">
            {DIMENSION_DESCRIPTIONS[dimensionKey] ?? `${dimension.label}ë³„ íˆìŠ¤í† ë¦¬ì…ë‹ˆë‹¤.`}
          </p>
        </div>
        <span className="text-xs text-muted-foreground">ì´ {NUMBER_FORMATTER.format(totalRows)}ê±´</span>
      </div>
      <div className="h-[260px]">
        {isLoading && chartData.length === 0 ? (
          <Skeleton className="h-full w-full" />
        ) : (
          <ChartContainer config={chartConfig}>
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={chartData} margin={{ top: 20, right: 16, left: 8, bottom: 8 }}>
                <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                <XAxis
                  dataKey="date"
                  tickFormatter={(value) => formatDateTick(value)}
                  className="text-xs text-muted-foreground"
                />
                <YAxis
                  width={50}
                  tickFormatter={(value) => NUMBER_FORMATTER.format(value)}
                  className="text-xs text-muted-foreground"
                />
                <ChartTooltip />
                <ChartLegend />
                {dimension.series.map((series, index) => {
                  const color = chartConfig[series.id]?.color ?? CATEGORY_COLORS[index % CATEGORY_COLORS.length]
                  return (
                    <Line
                      key={series.id}
                      type="monotone"
                      dataKey={series.id}
                      stroke={color}
                      strokeWidth={2}
                      dot={false}
                      name={series.key}
                    />
                  )
                })}
              </LineChart>
            </ResponsiveContainer>
          </ChartContainer>
        )}
      </div>
    </div>
  )
}

function SummaryRow({ label, value }) {
  return (
    <div className="flex items-center justify-between rounded-lg bg-muted/40 px-3 py-2">
      <span className="text-xs text-muted-foreground">{label}</span>
      <span className="text-sm font-semibold text-foreground">
        {typeof value === "number" ? NUMBER_FORMATTER.format(value) : value}
      </span>
    </div>
  )
}

function formatDateTick(value) {
  const parsed = Date.parse(`${value}T00:00:00`)
  if (!Number.isFinite(parsed)) return value
  return DATE_TICK_FORMATTER.format(new Date(parsed))
}
